.comics {
    &__list {
        margin-top: 45px;
        @media (max-width: 991.98px){
            margin-top: 30px;
        }
        @media (max-width: 767.98px){
            margin-top: 15px;
        }
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap:35px;
        @media (max-width: 991.98px){
            grid-template-columns: repeat(3, 1fr);
            gap: 15px;
        }
        @media (max-width: 767.98px){
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__item {
        opacity: 0;
        transform: translateY(-20%);
        visibility: hidden;
        display: flex;
        flex-direction: column;
        &-img {
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
            position: relative;
            padding-bottom: 120%;
            img {
                position: absolute;width: 100%;height: 100%;top: 0;left: 0;
                object-fit: cover;
            }
        }
        &-name {
            margin-top: 10px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
        }
        &-price {
            margin-top: 5px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
            color: rgba(0, 0, 0, 0.6);
            text-transform: uppercase;
        }
        &:hover {
            transform: translateY(-5px);
        }
        &.in-view {
            transition: all 0.3s ease-in-out; 
            opacity: 1;
            visibility: visible;
            transform: translateY(0); /* Apply your desired animation properties here */
        }
    }
}

