@import '../../style/variables.scss';

.app {
    &__banner {
        width: 100%;
        background-color: $dark;
        min-height: 100px;
        padding: 0 25px 0 45px;
        display: grid;
        grid-template-columns: 152px auto 133px;
        @media (max-width: 767.98px){
            grid-template-columns: 1fr;
            padding: 0;
            justify-items: center;
            text-align: center;
              img:first-child{
                display: none;
            }
        }
        &-text {
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            color: #FFFFFF;
            padding-left: 83px;
            padding-top: 18px;
            @media (max-width: 767.98px){
                padding: 20px;
            }
        }
    }
}